import React from 'react'
import { useNgrokUrl } from '../util/NgrokUrl'
import { useForm } from 'react-hook-form';
import { login } from '../util/LoginFetch';

const LoginPage = ({cookie, setCookie}) => {

    const { data } = useNgrokUrl();
    const { register, handleSubmit, reset, getValues } = useForm();

    const onSubmit = (v) => {

        let d = login(data.url, v.id, v.pass);
        d.then(res=> {
            if(res.id !== undefined){
                setCookie("data",{"id": res.id, "name": res.name});
            }else{
                alert(res.error)
            }
        })
       reset();
    }
    return (
        <div>LoginPage

            <form onSubmit={handleSubmit(onSubmit)}>
                <label>
                    <input type="text" {...register("id")} />
                </label>
                <label>
                    <input type="password" {...register("pass")} />
                </label>
                <input type="submit" value={"submit"}/>
            </form>
        </div>

    )
}

export default LoginPage