import './App.css';
import { useEffect, useState } from 'react';
import { useNgrokUrl } from './util/NgrokUrl';
import StorageProgress from './util/StorageProgress';
import LoginPage from './component/LoginPage';
import { useCookies } from "react-cookie";


function App() {

  const [state, setState] = useState();
  const {data} = useNgrokUrl();
  const [cookies, setCookie, removeCookie] = useCookies(["data"]);

  const fetchData = async(url) => {
    await fetch(url, {
      method: "GET",
      headers: {
        "Content-Type": "application/json;charset=utf-8",
        "ngrok-skip-browser-warning": "69420"
      }
    }).then(res=> res.json()).then(json=> setState(json));
  }

  const getStorageNum = () =>{
    let total = state.total;
    let free = state.free;
    let usage = total - free;
    return {
      total: Math.round((total / 1e+9) * 100) /100,
      free: Math.round((free / 1e+9) * 100) /100,
      usage: Math.round((usage / 1e+9) * 100) /100
    };
  }

  useEffect(()=>{
    if(data !== undefined){
      fetchData(data.url);
    }
  },[data])
  if(state === undefined) return <dir>Loading...</dir>
  if(cookies.data === undefined) return <><LoginPage cookie={cookies} setCookie={setCookie} /></>
  return (
    <div className="App">
      <br/>
        <StorageProgress width={"200px"} total={getStorageNum().total} free={getStorageNum().free} usage={getStorageNum().usage}/>      
    </div>
  );
}

export default App;
